                    <td colspan="4">水质自动监测仪器</td>
                    <td rowspan="2">分析方法</td>
                  </tr>
                  <tr>
                    <td>监测项目</td>
                    <td>型号</td>
                    <td>生产商</td>
                    <td>量程</td>
                  </tr>
                  <tr v-for="(item,index) in configurationData" :key="index">
                    <td>{{(index+1)}}</td>
                    <td>{{item.PROJECT_NAME}}</td>
                    <td>{{item.EQUIPMENT_MODEL}}</td>
                    <td>{{item.MANUFACTOR}}</td>
                    <td>{{item.EQUIPMENT_ORANGE}}</td>
                    <td>{{item.ANALY_METHONS}}</td>
                  </tr>
                  <!-- <tr>
                    <td>2</td>
                    <td>常规五参数</td>
                    <td>-</td>
                    <td>-</td>
                    <td>-</td>
                    <td>-</td>
                  </tr>
                  <tr>
                    <td>3</td>
                    <td>常规五参数</td>
                    <td>-</td>
                    <td>-</td>
                    <td>-</td>
                    <td>-</td>
                  </tr>
                  <tr>
                    <td>4</td>
                    <td>常规五参数</td>
                    <td>-</td>
                    <td>-</td>
                    <td>-</td>
                    <td>-</td>
                  </tr>
                  <tr>
                    <td>5</td>
                    <td>常规五参数</td>
                    <td>-</td>
                    <td>-</td>
                    <td>-</td>
                    <td>-</td>
                  </tr>
                  <tr>
                    <td>6</td>
                    <td>常规五参数</td>
                    <td>-</td>
                    <td>-</td>
                    <td>-</td>
                    <td>-</td>
                  </tr>
                  <tr>
                    <td>7</td>
                    <td>常规五参数</td>
                    <td>-</td>
                    <td>-</td>
                    <td>-</td>
                    <td>-</td>
                  </tr>
                  <tr>
                    <td>...</td>
                    <td>......</td>
                    <td>-</td>
                    <td>-</td>
                    <td>-</td>
                    <td>-</td>
                  </tr> -->
                  <tr>
                    <td rowspan="8">水质自动监测站质控措施</td>
                    <td>序号</td>
                    <td colspan="4">已实施的质控项目</td>
                    <td>备注</td>
                  </tr>
                  <tr>
                    <td>1</td>
                    <td colspan="4">24小时零点漂移核查和24小时跨度漂移核查</td>
                    <td>-</td>
                  </tr>
                  <tr>
                    <td>2</td>
                    <td colspan="4">标样核查</td>
                    <td>-</td>
                  </tr>
                  <tr>
                    <td>3</td>
                    <td colspan="4">多点线性核查</td>
                    <td>-</td>
                  </tr>
                  <tr>
                    <td>4</td>
                    <td colspan="4">实际水样对比</td>
                    <td>-</td>
                  </tr>
                  <tr>
                    <td>5</td>
                    <td colspan="4">加标回收率自动测定</td>
                    <td>-</td>
                  </tr>
                  <tr>
                    <td>6</td>
                    <td colspan="4">集成干预检查</td>
                    <td>-</td>
                  </tr>
                  <tr>
                    <td>...</td>
                    <td colspan="4">......</td>
                    <td>-</td>
                  </tr>
                  <tr>
                    <td>其他</td>
                    <td colspan="6"> - </td>
                  </tr>
                </table>
              </div>
            </div>
          </div>
          <div class="modular-item">
            <h5 class="modular-title">数据有效率情况</h5>
            <div class="modular-body">
              <el-table
                :data="effectivenessData"
                border
                style="width: 100%">
                <el-table-column
                  prop="projName"
                  label="监测项目">
                </el-table-column>
                <el-table-column
                  prop="yUploadNum"
                  label="应获取监测数据数量">
                </el-table-column>
                <el-table-column
                  prop="validNum"
                  label="获取有效数据数量">
                </el-table-column>
                <el-table-column
                  prop="validRate"
                  label="数据有效率">
                </el-table-column>
              </el-table>
            </div>
          </div>
          <div class="modular-item">
            <h5 class="modular-title">质控核查情况</h5>
            <div class="modular-body">
              <!--  w21003_spanLastValue w21003_spanValue w21003_zeroLastValue w21003_zeroValue -->
              <el-table
                :data="qualityVerificationData"
                border>
                <el-table-column label="监测项目">
                  <el-table-column
                    prop="time"
                    width="157"
                    label="时间"
                  ></el-table-column>
                </el-table-column>
                <el-table-column label="氨氮">
                  <el-table-column
                    prop="w21003_zeroValue"
                    label="零点核查"
                  ></el-table-column>
                  <el-table-column
                    prop="w21003_zeroLastValue"
                    width="118"
                    label="24小时零点漂移"
                  ></el-table-column>
                  <el-table-column
                    prop="w21003_spanValue"
                    label="跨度核查"
                  ></el-table-column>
                  <el-table-column
                    prop="w21003_spanLastValue"
                    width="118"
                    label="24小时跨度漂移"
                  ></el-table-column>
                </el-table-column>
                <el-table-column label="高锰酸盐指数">
                  <el-table-column
                    prop="w01019_zeroValue"
                    label="零点核查"
                  ></el-table-column>
                  <el-table-column
                    prop="w01019_zeroLastValue"
                    width="118"
