                  ></el-table-column>
                  <el-table-column
                    prop="w01019_spanValue"
                    label="跨度核查"
                  ></el-table-column>
                  <el-table-column
                    prop="w01019_spanLastValue"
                    width="118"
                    label="24小时跨度漂移"
                  ></el-table-column>
                </el-table-column>
                <el-table-column label="总磷">
                  <el-table-column
                    prop="w21011_zeroValue"
                    label="零点核查"
                  ></el-table-column>
                  <el-table-column
                    prop="w21011_zeroLastValue"
                    width="118"
                    label="24小时零点漂移"
                  ></el-table-column>
                  <el-table-column
                    prop="w21011_spanValue"
                    label="跨度核查"
                  ></el-table-column>
                  <el-table-column
                    prop="w21011_spanLastValue"
                    width="118"
                    label="24小时跨度漂移"
                  ></el-table-column>
                </el-table-column>
                <el-table-column label="总氮">
                  <el-table-column
                    prop="w21001_zeroValue"
                    label="零点核查"
                  ></el-table-column>
                  <el-table-column
                    prop="w21001_zeroLastValue"
                    width="118"
                    label="24小时零点漂移"
                  ></el-table-column>
                  <el-table-column
                    prop="w21001_spanValue"
                    label="跨度核查"
                  ></el-table-column>
                  <el-table-column
                    prop="w21001_spanLastValue"
                    width="118"
                    label="24小时跨度漂移"
                  ></el-table-column>
                </el-table-column>
              </el-table>
            </div>
          </div>
          <div class="modular-item">
            <h5 class="modular-title">常规五参数标样核查</h5>
            <div class="modular-body">
              <el-table
                :data="fiveParametTableData"
                border>
                <el-table-column label="监测项目">
                  <el-table-column
                    prop="time"
                    width="158"
                    label="监测日期"
                  ></el-table-column>
                </el-table-column>
                <el-table-column label="水温（℃）">
                  <el-table-column
                    prop="w01010_standardSamplesValue"
                    label="标液浓度"
                  ></el-table-column>
                  <el-table-column
                    prop="w01010_value"
                    label="测定结果"
                  ></el-table-column>
                  <el-table-column
                    prop="w01010_qualification"
                    label="是否合格"
                  ></el-table-column>
                </el-table-column>
                <el-table-column label="PH">
                  <el-table-column
                    prop="w01001_standardSamplesValue"
                    label="标液浓度"
                  ></el-table-column>
                  <el-table-column
                    prop="w01001_value"
                    label="测定结果"
                  ></el-table-column>
                  <el-table-column
                    prop="w01001_qualification"
                    label="是否合格"
                  ></el-table-column>
                </el-table-column>
                <el-table-column label="溶解氧">
                  <el-table-column
                    prop="w01009_standardSamplesValue"
                    label="标液浓度"
                  ></el-table-column>
                  <el-table-column
                    prop="w01009_value"
                    label="测定结果"
                  ></el-table-column>
                  <el-table-column
                    prop="w01009_qualification"
                    label="是否合格"
                  ></el-table-column>
                </el-table-column>
                <el-table-column label="电导率">
                  <el-table-column
                    prop="w01014_standardSamplesValue"
                    label="标液浓度"
                  ></el-table-column>
                  <el-table-column
                    prop="w01014_value"
                    label="测定结果"
                  ></el-table-column>
                  <el-table-column
                    prop="w01014_qualification"
                    label="是否合格"
                  ></el-table-column>
                </el-table-column>
                <el-table-column label="浊度">
                  <el-table-column
                    prop="w01003_standardSamplesValue"
                    label="标液浓度"
                  ></el-table-column>
                  <el-table-column
                    prop="w01003_value"
                    label="测定结果"
                  ></el-table-column>
                  <el-table-column
                    prop="w01003_qualification"
                    label="是否合格"
                  ></el-table-column>
                </el-table-column>
              </el-table>
            </div>
          </div>
          <div class="modular-item">
            <h5 class="modular-title">多点线性核查</h5>
            <div class="modular-body">
              <el-table
                :data="tableData"
                border
                height="100%"
                style="width: 100%">
                <el-table-column
                  prop="testingItems"
                  label="监测项目">
                </el-table-column>
                <el-table-column
                  prop="sequencing"
                  label="测定顺序">
                </el-table-column>
                <el-table-column
                  prop="standardSolution"
                  label="标准溶液浓度（mg/L）">
                </el-table-column>
                <el-table-column
                  prop="measuredValue"
                  label="测定值（mg/L）">
                </el-table-column>
                <el-table-column
                  prop="accuracy"
                  label="准确度">
                </el-table-column>
                <el-table-column
                  prop="determination"
                  label="测定结果">
                </el-table-column>
              </el-table>
            </div>
          </div>
          <div class="modular-item">
            <h5 class="modular-title">集成干预检查</h5>
            <div class="modular-body">
              <el-table
                :data="interventionTableData"
                border>
                <el-table-column
                  prop="indexName"
                  label="监测项目">
                </el-table-column>
                <el-table-column
                  prop="artificiaSamplingValue"
                  label="系统测试结果（mg/L）">
                </el-table-column>
                <el-table-column
                  prop="automaticSamplingValue"
                  label="仪器测试结果（mg/L）">
                </el-table-column>
                <el-table-column
                  prop="error"
                  label="相对偏差（%）">
                </el-table-column>
                <el-table-column
                  prop="qualification"
                  label="是否合格">
                </el-table-column>
              </el-table>
            </div>
          </div>
          <div class="modular-item">
            <h5 class="modular-title">加标回收率</h5>
            <div class="modular-body">
              <el-table
                :data="additionRecoveryData"
                border>
                <el-table-column
                  prop="indexName"
                  label="监测项目">
                </el-table-column>
                <el-table-column
                  prop="sampleVolume"
                  label="样品体积（mg/L）">
                </el-table-column>
                <el-table-column label="加标样">
                  <el-table-column
                    prop="addConcentration"
                    label="加标液浓度（mg/L）">
                  </el-table-column>
                  <el-table-column
                    prop="addVolume"
                    label="加标体积（mL）">
                  </el-table-column>
                </el-table-column>
                <el-table-column
                  prop="value"
                  width="154px"
                  label="加标前样品测定结果（mg/L）">
                </el-table-column>
                <el-table-column
                  prop="addValue"
                  width="154px"
                  label="加标后样品测定结果（mg/L）">
                </el-table-column>
                <el-table-column
                  prop="recoveryValue"
                  label="加标回收率">
                </el-table-column>
                <el-table-column
                  prop="qualification"
                  label="是否合格">
                </el-table-column>
              </el-table>
            </div>
          </div>
          <div class="modular-item">
            <h5 class="modular-title">实际水样比对</h5>
            <div class="modular-body">
              <el-table
                :data="waterSampleData"
                border>
                <el-table-column
                  type="index"
                  label="序号">
                </el-table-column>
                <el-table-column
                  prop="indexName"
                  label="监测项目">
                </el-table-column>
                <el-table-column
                  prop="indexUnit"
                  label="单位">
                </el-table-column>
                <el-table-column
                  prop="automaticValue"
                  label="仪器测定结果">
                </el-table-column>
                <el-table-column
                  prop="artificialValue"
                  label="实验室测定结果">
