                </el-table-column>
              </el-table>
            </div>
          </div>
        </el-scrollbar>
      </div>
  </div>
</template>

<script>
export default {
  name: 'singleMonthAnalysis',
  data(){
    return {
      tableData:[],
      stationId:'',
      stationName:'',
      presentation:'',
      sTime:'',
      eTime:'',
      effectivenessData:[],
      configurationData:[],
      qualityVerificationData:[],
      fiveParametTableData:[],
      interventionTableData:[],
      additionRecoveryData:[],
      waterSampleData:[]
    }
  },
  methods:{
    //查询单站月数据有效率情况(数据有效率情况)
    getEffectivenessData(){
      this.$http.get('/Quality/qualityAnalysis/effectivenessData',{
        params:{
          startTime: this.sTime,
          endTime: this.eTime,
          stationCodes: this.stationId,
        }
      }).then(res =>{
        if(res.data.code === 0){
          this.effectivenessData = res.data.content.dataList;
        }
      }).catch(error =>{

      })
    },
    //查询单站月质控考核分析(仪器配置情况)
    monthExamineInstrumentData(){
      this.$http.get('/Quality/qualityAnalysis/singleMonthExamineInstrumentData',{
        params:{
            // startTime: this.sTime,
            // endTime: this.eTime,
            stationCodes: this.stationId,//
        }
      }).then((res)=>{
        if(res.data.code === 0){
          let list = res.data.content.dataList;
          this.configurationData = list;
          console.log('仪器配置情况',list);
        }
      }).catch((error)=>{

      });
    },
    //查询单站月质控考核分析(常规五参数)
    fiveParameterData(){
      this.$http.get('/Quality/qualityAnalysis/singleMonthExamineFiveParameterData',{
        params:{
            startTime: this.sTime,
            endTime: this.eTime,
            stationCodes: this.stationId
        }
      }).then((res)=>{
        if(res.data.code === 0){
          let list = res.data.content.dataList;
          console.log('常规五参数',list);
          this.fiveParametTableData = list;
        }
      }).catch((error)=>{

      });
    },
    //查询单站月质控考核分析(集成干预)
    interventionData(){
      this.$http.get('/Quality/qualityAnalysis/singleMonthExamineIntegratedInterventionData',{
        params:{
            startTime: this.sTime,
            endTime: this.eTime,
            stationCodes: this.stationId
        }
      }).then((res)=>{
        if(res.data.code === 0){
          let list = res.data.content.dataList;
          this.interventionTableData = list;
          console.log('集成干预',list);
        }
      }).catch((error)=>{

      });
    },
     //查询单站月质控考核分析(多点线性)
    multipointlinearityData(){
      this.$http.get('/Quality/qualityAnalysis/singleMonthExamineMultipointlinearityData',{
        params:{
            startTime: this.sTime,
            endTime: this.eTime,
            stationCodes: this.stationId
        }
      }).then((res)=>{
        if(res.data.code === 0){
          let list = res.data.content.dataList;
          console.log('多点线性',list);
        }
      }).catch((error)=>{

      });
    },
     //查询单站月质控考核分析(质控核查情况)
    monthExamineQualityData(){
      this.$http.get('/Quality/qualityAnalysis/singleMonthExamineQualityData',{
        params:{
            startTime: this.sTime,
            endTime: this.eTime,
            stationCodes: this.stationId
        }
      }).then((res)=>{
        if(res.data.code === 0){
          let list = res.data.content.dataList;
          console.log('质控核查情况',list);
          //controlVerificationData
          this.qualityVerificationData = list;
        }
      }).catch((error)=>{

      });
    },
    //查询单站月质控考核分析(实际水样)
    waterSampleComparisonData(){
      this.$http.get('/Quality/qualityAnalysis/singleMonthExamineRealWaterSampleComparisonData',{
        params:{
            startTime: this.sTime,
            endTime: this.eTime,
            stationCodes: this.stationId
        }
      }).then((res)=>{
        if(res.data.code === 0){
          let list = res.data.content.dataList;
          this.waterSampleData = list;
          console.log('实际水样',list);
        }
      }).catch((error)=>{

      });
    },
    //查询单站月质控考核分析(加标回收)
    monthExamineRecoveryData(){
      this.$http.get('/Quality/qualityAnalysis/singleMonthExamineRecoveryData',{
        params:{
            startTime: this.sTime,
            endTime: this.eTime,
            stationCodes: this.stationId
        }
      }).then((res)=>{
        if(res.data.code === 0){
          let list = res.data.content.dataList;
          this.additionRecoveryData = list;
          console.log('加标回收',list);
        }
      }).catch((error)=>{

      });
    }
  },
  mounted(){
    let nextMounth = new Date(new Date().setMonth(new Date().getMonth()+1));
    this.sTime = new Date(new Date().setDate(1)).format('yyyy-MM-dd');
    this.eTime = new Date(nextMounth.setDate(0)).format('yyyy-MM-dd');
  },
  created(){
    this.$store.commit("setShowlefttree", true);
  },
  computed: {
    stationInfo: {
      get: function() {
        return this.$store.state.stationInfo;
      }
    }
  },
  watch: {
    stationInfo(value) {
      if (value.length > 0) {
        this.stationId = value[0].nodeId;
        this.stationName = value[0].nodeName;
        console.log(this.stationId);
        this.monthExamineInstrumentData();
        this.fiveParameterData();
        this.interventionData();
        this.multipointlinearityData();
        this.monthExamineQualityData();
        this.waterSampleComparisonData();
        this.monthExamineRecoveryData();
        this.getEffectivenessData();
      }
    }
  }
}
</script>

<style scoped lang="less">
.single-month-analysis{
  height: 100%;
  padding: 0 20px;
  .title-wrap{
    text-align: center;
    line-height: 24px;
    padding-top: 10px;
    .title{
      font-size: 16px;
    }
  }
  .single-modular-wrap{
    height: calc(100% - 58px);
  }
  .modular-title{
    color: #166bce;
    font-size: 14px;
    padding: 10px 0;
  }
  .modular-title:before{
    content: '';
    display: inline-block;
    vertical-align: middle;
    margin-bottom: 2px;
    width: 8px;
    height: 8px;
    background: #166bce;
    margin-right: 4px;
  }
  .modular-body{
    padding: 0 10px;
  }

}
.normal-table-wrap{
  table{
    width: 100%;
    border-collapse: collapse;
    border: 1px solid #d6e3f2;
    td{
      border: 1px solid #d6e3f2;
      padding: 6px 8px;
      text-align: center;
    }
  }
}
</style>



